import React, { useState } from "react";
import logo from "../../styles/assets/images/Logo/favicon-removebg.png";
import { Fragment } from "react";
import { Link } from "gatsby";
import { Navbar, Container, Nav } from "react-bootstrap";
import { Dropdown } from "react-bootstrap";

const Header = () => {
  const [toggleShow, setToggleShow] = useState(false);
  const handleToggleShow = () => setToggleShow(!toggleShow);

  return (
    <Fragment>
      <header>
        <Navbar expand="lg" className={"navbar navbar-default header"}>
          <Container fluid>
            <Navbar.Brand className="navbar-brand">
              <Link className="ms-5" href="/">
                <img className="logoheader" src={logo} alt="logo-img" />
              </Link>
            </Navbar.Brand>
            <Navbar.Toggle
              onClick={handleToggleShow}
              className={`bg-transparent navbar-toggler x${
                toggleShow ? "collapsed" : ""
              }`}
            >
              <div className="icon-bar"></div>
              <div className="icon-bar"></div>
              <div className="icon-bar"></div>
            </Navbar.Toggle>
            <Navbar.Collapse>
              <Nav className="navbar-nav nav-underline ms-auto d-flex justify-content-around align-items-center">
                <li className="nav-item">
                  <Dropdown>
                    <Dropdown.Toggle
                      as={"a"}
                      className="nav-link fw-bold text-white text-uppercase fs-3"
                    >
                      Services
                    </Dropdown.Toggle>
                    <Dropdown.Menu>
                      <Link to="/mern-development">
                        MERN / MEAN Development
                      </Link>
                      <Link>
                        <Link to="/smart-contract">Smart Contract</Link>
                      </Link>
                    </Dropdown.Menu>
                  </Dropdown>
                  {/* <a
                    className="nav-link fw-bold text-uppercase fs-3"
                    aria-current="page"
                    href="#"
                  >
                    Services
                  </a> */}
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link  fw-bold text-uppercase fs-3"
                    href="/blogs"
                  >
                    Blogs
                  </Link>
                </li>
                <li className="nav-item">
                  <a className="nav-link fw-bold text-uppercase fs-3" href="#">
                    our team
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link  fw-bold text-uppercase fs-3" href="#">
                    about us
                  </a>
                </li>
                <li className="nav-item text-uppercase">
                  <button className="btn btn-light btn--outline text-dark fw-bold text-uppercase">
                    talk to us
                  </button>
                </li>
              </Nav>
            </Navbar.Collapse>
          </Container>
        </Navbar>
        {/* <nav className="navbar navbar-expand-lg header">
          <div className="container-fluid">
            <Link className="navbar-brand ms-5" href="/">
              <img className="logoheader" src={logo} alt="logo-img" />
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarNavDropdown"
              aria-controls="navbarNavDropdown"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="my-1 mx-2 close">X</span>
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNavDropdown">
              <ul className="navbar-nav nav-underline ms-auto d-flex justify-content-around align-items-center">
                <li className="nav-item">
                  <a
                    className="nav-link fw-bold text-uppercase fs-3"
                    aria-current="page"
                    href="#"
                  >
                    Services
                  </a>
                </li>
                <li className="nav-item">
                  <Link
                    className="nav-link  fw-bold text-uppercase fs-3"
                    href="/blogs"
                  >
                    Blogs
                  </Link>
                </li>
                <li className="nav-item">
                  <a className="nav-link fw-bold text-uppercase fs-3" href="#">
                    our team
                  </a>
                </li>
                <li className="nav-item">
                  <a className="nav-link  fw-bold text-uppercase fs-3" href="#">
                    about us
                  </a>
                </li>
                <li className="nav-item text-uppercase">
                  <button className="btn btn-light btn--outline text-dark fw-bold text-uppercase">
                    talk to us
                  </button>
                </li>
              </ul>
            </div>
          </div>
        </nav> */}
      </header>
    </Fragment>
  );
};

export default Header;
